.main{
    width: 100vw;
    height: 100vh; 
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
  }
  .ant-modal-content{
    text-align: center !important;
    .modalMain{
      width: 100%;
      height: 100%;
      margin: 50px 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .btn{
        width: 200px;
        height: 100px;
      }
      .customBtn{
        width: 300px;
        height: 100px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
      }
    }
  }